        DxButton,
        // DXButton,
        // DxFormItem,
        // penSvg,
        // trashSvg,
        AddTagRegistration,
        EditTagRegistration
    },
    created() {
        this.$store.commit("UPDATE_CURRENT_PAGENAME", {
            subpageName: "TAG REGISTRATION",
            subpageInnerName: null,
        });
        if (this.$store.state.status.server == true) {
            this.tagRegistrationList = [
                {
                    id: 1,
                    id_platform: 'MDC',
                    tag_no: 'MDC-CC-04201',
                    description: '-',
                    wa: true,
                    mb: true,
                    rci: true,
                    cc: false,
                    erp: false
                },
                {
                    id: 2,
                    id_platform: 'MDF',
                    tag_no: 'MDE-CC-07201',
                    description: '-',
                    wa: false,
                    mb: false,
                    rci: true,
                    cc: true,
                    erp: false
                },
                {
                    id: 3,
                    id_platform: 'MDPP',
                    tag_no: 'MDPP-CP-1210',
                    description: '-',
                    wa: true,
                    mb: false,
                    rci: false,
                    cc: true,
                    erp: false
                }
            ];
            console.log(this.baseURL);
            this.FETCH_TAG();
        }
    },
    data() {
        return {
            tagRegistrationList: [],
            system: null,
            isShow: 0,
            selectedId: null,
            gridRefName: "grid",
            isPipeline: false,
        };
    },
    computed: {
        baseURL() {
            var mode = this.$store.state.mode;
            if (mode == "dev") return this.$store.state.modeURL.dev;
            else if (mode == "prod") return this.$store.state.modeURL.prod;
            else return console.log("develpment mode set up incorrect.");
        },
    },
    methods: {
        EXPORT_DATA(e) {
            const workbook = new Workbook();
            const worksheet = workbook.addWorksheet("Projects");
            exportDataGrid({
                worksheet: worksheet,
                component: e.component
            }).then(function () {
                workbook.xlsx.writeBuffer().then(function (buffer) {
                    saveAs(
                        new Blob([buffer], { type: "application/octet-stream" }),
                        "inspection_record.xlsx"
                    );
                });
            });
            e.cancel = true;
        },
        FETCH_TAG() {
            this.system = this.$route.fullPath.split('/')[2];
            this.isPipeline = this.system == 'pipeline' ? true : false;
            this.isShow = 0;
            // this.isLoading = true;
            // axios({
            //     method: "get",
            //     url:
            //         "/ManagementOfChange",
            //     headers: {
            //         Authorization: "Bearer " + JSON.parse(localStorage.getItem("token"))
            //     }
            // })
            //     .then(res => {
            //         console.log("insp record:");
            //         console.log(res);
            //         if (res.status == 200 && res.data) {
            //             this.tagRegistrationList = res.data;
            //         }
            //     })
            //     .catch(error => {
            //         console.log(error);
            //     })
            //     .finally(() => {
            //         this.isLoading = false;
            //     });
        },
        ADD_ROW() {
            this.isShow = 1
        },
    }
};
</script>

